    line-height: 120%;
}
.home_ser--title .note-lg{
    font-size: 1.8rem;
    line-height: 120%;
}
.home_ser--img{
    padding-top: calc(100% * 270/384);
}
.home_nb--img{
    padding-top: calc(100% * 400/800);
}
.home_nb--list .box-navi{
    padding-top: calc(100%* 210 / 800);
}
.advise{
    padding: var(--size-pd) 0;
    margin: var(--size-pd) 0;
}
.advise .title-main{
    margin-bottom: 2rem;
}
.advise-row{
    justify-content: center;
}
.advise-col{
    width: calc(4/12 * 100%);
    padding: 0 calc(var(--col-gap) / 2);
    margin-bottom: var(--row-gap);
}
.advise-item{
    height: 100%;
    display: flex;
    display: -webkit-flex;
    justify-content: space-between;
    flex-direction: column;
    align-items: center;
    gap: 1rem;
}
.advise-img{
    height: 100%;
    width: 100%;
    padding-top: calc(300/500 * 100%);
    position: relative;
    overflow: hidden;
    border-radius: 1.2rem;
}

.advise-img img{
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    display: block;
    width: 100%;
    height: 100%;
    object-fit: cover;
    transition: all .3s ease;
}
.advise-img:hover img{
transform: scale(1.1) translate(-50%, -50%);
}
.advise-txt{
    font-size: 1.8rem;
    font-weight: 700;
    text-align: center;
    transition: all .3s ease;
}
.advise-txt:hover{
    color: #232f9a;
}
.ser_tabs--wrap .sp_des--tab-link {
    min-width: 22.4rem;
    display: flex;
    align-items: center;
    justify-content: center;
}
.ser_tabs{
    position: sticky;
    background-color: #ffffff;
        top:  10rem;
    padding-top: 1rem;
    padding-bottom: 1rem;
    border: none;
    z-index: 1100;

    @media screen and (max-width:1200px) {
        top: 10.2rem;
    }

    @media screen and (max-width:600px) {
        top: 9.2rem;
    }

    @media screen and (max-width:460px) {
        top: 7.4rem;
    }
}
